/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import {useStaticQuery, graphql} from "gatsby"
import IndexHeader from "./headers/headerIndex"
import "./layout.css"
import Header from "./headers/header"
import ProductFooter from "../components/productFooter"
import FooterIndex from "../components/footer"


const Layout = ({children, indexHeader, footer, sideNavigation, urlPage}) => {

    const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

    return (
        <>
            <div style={{
                minHeight: `100vh`,
                position: "relative",
                overflow: `hidden`
            }}>
                {(() => {
                        if (indexHeader === true) {
                            return (
                                <div>
                                    <IndexHeader siteTitle={data.site.siteMetadata.title} disableNavBar={true}/>
                                    <Header selectedItem={urlPage} linksData={[
                                        {"url": "/", "text": "Vitaminas"},
                                        {"url": "/vitaminaA", "text": "Vitamina A"},
                                        {
                                            "url": "/vitaminaB",
                                            "text": "Vitamina B",
                                        },
                                        {"url": "/vitaminaC", "text": "Vitamina C"},
                                        {"url": "/vitaminaD", "text": "Vitamina D"},
                                        {"url": "/vitaminaE", "text": "Vitamina E"},
                                        {"url": "/vitaminaK", "text": "Vitamina K"},
                                    ]}/>
                                </div>
                            )
                        } else {
                            return <Header siteTitle={data.site.siteMetadata.titled} linksData={[
                                {"url": "/", "text": "Vitaminas"},
                                {"url": "/vitaminaA", "text": "Vitamina A"},
                                {
                                    "url": "/vitaminaB",
                                    "text": "Vitamina B",

                                },
                                {"url": "/vitaminaC", "text": "Vitamina C"},
                                {"url": "/vitaminaD", "text": "Vitamina D"},
                                {"url": "/vitaminaE", "text": "Vitamina E"},
                                {"url": "/vitaminaK", "text": "Vitamina K"},
                            ]}/>
                        }
                    }
                )()}

                {(() => {
                        if (sideNavigation === true) {
                            return (
                                <div>
                                    <div id="leftSideNav"
                                         style={{
                                             width: `20%`,
                                             float: `left`,
                                         }}>
                                    </div>

                                    <div
                                        style={{
                                            margin: `0 auto`,
                                            maxWidth: '60%',
                                            padding: `0 1.0875rem 1.45rem`,
                                            height: `100%`,
                                            position: "relative",
                                            float: `left`

                                        }}
                                    >
                                        <div style={{height: `50px`}}>
                                        </div>
                                        <main>{children}</main>

                                        {(() => {

                                                return <ProductFooter/>

                                            }
                                        )()}
                                    </div>

                                    <div id="rightSideNav"
                                         style={{
                                             width: `20%`,
                                             float: `right`,
                                         }}>
                                    </div>
                                </div>
                            )
                        } else {
                            return (
                                <div
                                    style={{
                                        margin: `0 auto`,
                                        maxWidth: '80%',
                                        padding: `0 1.0875rem 1.45rem`,
                                        height: `100%`,
                                        position: "relative"

                                    }}
                                >
                                    <div style={{height: `50px`}}>
                                    </div>
                                    <main>{children}</main>

                                    {(() => {

                                            return <ProductFooter/>

                                        }
                                    )()}
                                </div>
                            )
                        }
                    }
                )()}


            </div>
        </>
    )
}

Layout.propTypes = {
    children: PropTypes.node.isRequired,
}

export default Layout
